'use client';

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { MapPin, AlertTriangle, List } from 'lucide-react';
import { MapComponent } from '@/components/map';
import { incidents as allIncidents, Incident } from '@/lib/incidents';

export default function AccidentsNearYouPage() {
  const [location, setLocation] = useState<{ latitude: number; longitude: number } | null>(null);
  const [nearbyAccidents, setNearbyAccidents] = useState<Incident[]>([]);
  const [selectedIncident, setSelectedIncident] = useState<Incident | null>(null);

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          });
        },
        (error) => {
          console.error("Error getting location:", error);
        }
      );
    }
  }, []);

  useEffect(() => {
    if (location) {
      const accidents = allIncidents.filter(incident => incident.category === 'Accident');
      // For now, we'll just show all accidents as "nearby"
      // In a real app, you'd calculate distance and filter.
      setNearbyAccidents(accidents);
    }
  }, [location]);

  const handleOpenMap = (incident: Incident) => {
    setSelectedIncident(incident);
  };
  
  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Accidents Near You</h1>
          <p className="mt-4 text-lg text-muted-foreground">Stay informed about recent accidents in your vicinity.</p>
        </header>

        {location ? (
          <div className="space-y-6">
            {nearbyAccidents.length > 0 ? (
              nearbyAccidents.map((incident) => (
                <Card key={incident.id} className="shadow-md hover:shadow-lg transition-shadow">
                  <CardHeader className="flex flex-row items-center justify-between">
                    <div className="space-y-1.5">
                      <CardTitle className="font-headline text-2xl flex items-center gap-2">
                         <AlertTriangle className="w-6 h-6 text-destructive" />
                         {incident.category}
                      </CardTitle>
                      <CardDescription>Severity: {incident.severity}</CardDescription>
                    </div>
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button variant="outline" size="icon" onClick={() => handleOpenMap(incident)}>
                          <MapPin className="w-5 h-5" />
                        </Button>
                      </DialogTrigger>
                      <DialogContent className="max-w-3xl h-auto">
                        <DialogHeader>
                          <DialogTitle>Accident Location</DialogTitle>
                        </DialogHeader>
                        {selectedIncident && (
                           <div className="h-[400px] w-full mt-4">
                             <MapComponent
                                showAccidents
                                showPowerCuts={false}
                                showWaterLogging={false}
                                center={{ lat: selectedIncident.lat, lng: selectedIncident.lng }}
                                zoom={15}
                              />
                           </div>
                        )}
                      </DialogContent>
                    </Dialog>
                  </CardHeader>
                  <CardContent>
                    <p className="text-muted-foreground">{incident.description}</p>
                    <div className="flex items-center text-sm text-muted-foreground mt-4">
                      <MapPin className="w-4 h-4 mr-2" />
                      <span>Lat: {incident.lat.toFixed(5)}, Lng: {incident.lng.toFixed(5)}</span>
                    </div>
                  </CardContent>
                </Card>
              ))
            ) : (
              <Card>
                <CardContent className="p-8 text-center">
                  <p className="text-muted-foreground">No accidents reported near you.</p>
                </CardContent>
              </Card>
            )}
          </div>
        ) : (
          <div className="text-center text-muted-foreground">
            <p>Detecting your location to find nearby accidents...</p>
          </div>
        )}
      </main>
    </div>
  );
}
