'use client';

import React from 'react';
import { GoogleMap, useJsApiLoader, OverlayView } from '@react-google-maps/api';
import { CustomMapPin } from './custom-map-pin';
import { Droplets, Car, PowerOff } from 'lucide-react';
import { incidents as allIncidents } from '@/lib/incidents';

const defaultContainerStyle = {
  width: '100%',
  height: '100%',
  borderRadius: '0.5rem',
};

const defaultCenter = {
  lat: 37.7749,
  lng: -122.4194,
};

const getPixelPositionOffset = (width: number, height: number) => ({
  x: -(width / 2),
  y: -(height / 2),
});

const categoryStyles: { [key: string]: { icon: React.ElementType; className: string } } = {
    'Water Logging': {
        icon: Droplets,
        className: 'bg-blue-100 border-blue-500 text-blue-500',
    },
    'Accident': {
        icon: Car,
        className: 'bg-red-100 border-red-500 text-red-500',
    },
    'PowerCut': {
        icon: PowerOff,
        className: 'bg-yellow-100 border-yellow-500 text-yellow-500',
    },
};

interface MapComponentProps {
  showWaterLogging: boolean;
  showAccidents: boolean;
  showPowerCuts: boolean;
  center?: { lat: number; lng: number };
  zoom?: number;
  containerStyle?: React.CSSProperties;
}

export function MapComponent({ 
  showWaterLogging, 
  showAccidents, 
  showPowerCuts,
  center = defaultCenter,
  zoom = 12,
  containerStyle = defaultContainerStyle
}: MapComponentProps) {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;

  const { isLoaded, loadError } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: apiKey || '',
  });

  if (loadError) {
    return (
      <div className="flex items-center justify-center h-full bg-muted rounded-lg">
        <p className="text-destructive">
          Error loading maps.
        </p>
      </div>
    );
  }

  if (!isLoaded) {
    return (
       <div className="flex items-center justify-center h-full bg-muted rounded-lg">
        <p>Loading Map...</p>
      </div>
    );
  }

  if (!apiKey) {
    return (
      <div className="flex items-center justify-center h-full bg-muted rounded-lg">
        <p className="text-destructive">
          Google Maps API key is missing. Please add it to your .env file.
        </p>
      </div>
    );
  }
  
  const incidentsToDisplay = allIncidents.filter(incident => {
      if (showWaterLogging && incident.category === 'Water Logging') return true;
      if (showAccidents && incident.category === 'Accident') return true;
      if (showPowerCuts && incident.category === 'PowerCut') return true;
      return false;
  });

  const incidents = zoom > 14 ? incidentsToDisplay.filter(i => i.lat === center.lat && i.lng === center.lng) : incidentsToDisplay;


  return (
    <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={zoom}>
      {incidents.map(incident => {
          const style = categoryStyles[incident.category];
          if (!style) return null;
          return (
            <OverlayView
              key={incident.id}
              position={{ lat: incident.lat, lng: incident.lng }}
              mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
              getPixelPositionOffset={getPixelPositionOffset}
            >
              <CustomMapPin icon={style.icon} className={style.className} />
            </OverlayView>
          )
      })}
    </GoogleMap>
  );
}
